:root {
  --base-color-pink: #f16d7f;
  --base-color-text: #222222;
}

/* Общие стили */
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', sans-serif;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.container {
  max-width: 1170px;
}

.center {
  padding-left: calc(50% - 585px);
  padding-right: calc(50% - 585px);
}

/* Стили для Header */
.header {
  margin-top: 25px;
  margin-bottom: 25px;
  background-color: #fff;
  position: relative;
  z-index: 10;
}

.header__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.header__logo {
  padding-right: 39px;
}

.header__dropdown {
  position: relative;
  width: 87px;
  height: 38px;
}

.header__form {
  display: flex;
  align-items: center;
}

.header__title {
  font-size: 14px;
  line-height: 32px;
  font-weight: 400;
  text-align: left;
  text-transform: uppercase;
  letter-spacing: 0.025em;
  color: var(--base-color-text);
  border-bottom: 1px solid #e8e8e8;
}

.header__link {
  margin-top: 5px;
  float: none;
  color: var(--base-color-text);
  font-size: 14px;
  line-height: 28px;
  font-weight: 400;
  letter-spacing: 0.025em;
  display: block;
  text-align: left;
}

.header__link:hover {
  color: var(--base-color-pink);
  background-color: #fff;
}

.header__control {
  width: 15vw;
  min-height: 38px;
  padding: 9px 16px 10px;
  border: solid 1px #e6e6e6;
}

.header__control::placeholder {
  font-family: 'Lato', sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #a4a4a4;
}

.header__search {
  width: 3vw;
  min-height: 38px;
  border: solid 1px #e6e6e6;
  background-color: #fff;
  cursor: pointer;
}

.header__right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  overflow: hidden;
}

.header__basket {
  position: relative;
  float: left;
}

.header__basket-drd {
  margin-right: 26px;
  border: none;
  background-color: #fff;
  cursor: pointer;
}

.header__basket-count {
  position: absolute;
  top: -1px;
  left: 0;
  background-color: var(--base-color-pink);
  color: white;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 700;
}

.header__content-basket {
  display: none;
  position: fixed;
  padding: 10px;
  border: 1px solid #e8e8e8;
  background-color: #fff;
  border-radius: 5px;
  width: 355px;
  z-index: 1000;
}

.header__basket-drd .header__content-basket.show {
  display: block !important;
}

.cart__number-container {
  width: 60px;
}

.cart__number {
  width: 100%;
  height: 30px;
  background-color: #ffffff;
  border: 1px solid #eaeaea;
  text-align: center;
  outline: none;
}

.cart__number::-webkit-inner-spin-button,
.cart__number::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.cart__number[type="number"] {
  -moz-appearance: textfield;
}

.header__basket-item-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__basket-box {
  display: flex;
  align-items: center;
  color: var(--base-color-text);
  padding: 10px;
}

.header__basket-item {
  padding-left: 10px;
  padding-right: 10px;
}

.header__basket-text {
  padding: 5px;
  font-size: 12px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--base-color-text);
}

.header__basket-cash {
  padding: 5px;
  font-size: 12px;
  line-height: 1.2;
  font-weight: 400;
  color: var(--base-color-pink);
}

.header__basket-times {
  padding-left: 15px;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  color: #c0c0c0;
  cursor: pointer;
}

.header__basket-times:hover {
  color: var(--base-color-pink);
}

.header hr {
  border: 1px solid rgba(237, 237, 237, 0.74);
}

.header__basket-total {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--base-color-text);
  padding-top: 10px;
}

.header__basket-btn {
  display: grid;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.header__basket-check {
  font-size: 14px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #4a4a4a;
  border: 1px solid #eaeaea;
  width: 12vw;
  padding: 20px;
}

.header__basket-check:hover {
  border: 1px solid var(--base-color-pink);
  color: var(--base-color-pink);
}

.header__dropdown-account {
  position: relative;
}

.header__btn-account {
  width: 140px;
  height: 38px;
  background-color: var(--base-color-pink);
  border: solid 1px var(--base-color-pink);
  font-size: 15px;
  font-weight: 400;
  color: #fff;
  cursor: pointer;
}

.header__btn-account:hover {
  background-color: #fff;
  transition: all 0.5s;
}

.header__dropdown-account-content {
  padding: 10px 30px 10px 30px;
  text-align: center;
  color: #fff;
}

.header__dropdown-account-content:hover {
  color: var(--base-color-pink);
}
